  .hero .floating-box {
    position: absolute;
    object-fit: cover;
    color: white;
    background-color:var(--card);
  
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px;
    transition: all 2.5s cubic-bezier(0.23, 0.9, 1, 1);
  }
  
  .floating-box--primary {
    height: 100px;
    width: 100px;
  }
  
  .floating-box--secondary {
    height: 65px;
    width: 65px;
  }
  
  .floating-box--outlined {
    height: 48px !important;
    width: 48px !important;
    background-color: transparent !important;
    border: 2px solid var(--card);
  }
  
  .floating-box--empty {
    height: 56px !important;
    width: 56px !important;
    opacity: 0.7;
  }
  
